.navbar {
    width: 100%;
    background-color: #171717;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.5rem 1rem;
    z-index: 50;
}

.navbar__logo h2 {
    color: #f4f4f4;
    font-size: 1.4rem;
    cursor: pointer;
}

.navbar__links {
    display: flex;
    list-style: none;
    align-items: center;
}

.navbar__links>li {
    padding-left: 1.4rem
}

.navbar__links>li>a {
    text-decoration: none;
    color: #f4f4f4;
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar__links>li>a>span {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 8px;
}

.cart__link {
    padding: 8px;
    background-color: #333;
    border-radius: 8px;
}

.cart__link:hover {
    background-color: #dd219e;
    color: #f4f4f4;
}

.cartlogo__badge {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: #f4f4f4;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 8px;
    color: #171717;
    font-size: 1rem;
}

.hamburger__menu {
    width: 30px;
    height: 30px;
    display: none;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
}

.hamburger__menu:hover>div {
    background-color: #dd219e;
}


.hamburger__menu>div {
    width: 100%;
    height: 3px;
    background-color: #f4f4f4;
}

@media (max-width: 960px){

    .navbar__links{
        display: none;
    }

    .hamburger__menu{
        display: flex;
    }  
}

@media (max-width: 500px){
    .navbar__logo h2 {
        font-size: 1rem;
    }
}